.cards {
    justify-content: flex-start;
    flex-wrap: wrap;
    display: flex;
    margin: 160px auto 0;
    gap: 0 32px;
}

.card {
    position: relative;
    width: 336px;
    height: 425px;
    border-radius: 32px;
    overflow: hidden;
    margin-bottom: 32px;
}
.card:last-child{
    margin-bottom: 128px;
}
.card__image-wrapper {
    position: relative;
    width: 100%;
    height: 100%;
}

.card__image-wrapper::before {
    background: linear-gradient(180deg, transparent 48.96%,rgba(14,13,19,.9) 89.58%);
    content: "";
    height: 100%;
    left: 0;
    position: absolute;
    top: 0;
    width: 100%;
    z-index: 1;
}

.card__image {
    position: absolute;
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.card__name {
    position: absolute;
    left: 32px;
    bottom: 24px;
    z-index: 1;
    min-height: 24px;
    width: 100%;
}

.card__title {
    color: white;
    font-size: 24px;
}

.card__subtitle {
    width: 192px;
    color: #C4C1CF;
    font-weight: 600;
    font-size: 18px;
}

.card__information {
    position: absolute;
    width: 80px;
    height: 80px;
    background-color: #201f2e;
    border-radius: 24px;
    left: 18px;
    top: 22px;
    display: flex;
    flex-direction: column;
    align-items: end;
    justify-content: center;
    padding: 21px 8px;
}

.card__information-value {
    color: white;
    font-weight: 700;
    font-size: 24px;
    line-height: 28px;
}

.card__information-title {
    color: #C4C1CF;
    font-weight: 300;
    font-size: 14px;
    line-height: 16px;
}
.search {
    top: 40px;
    position: relative;
    font-size: 24px;
    font-weight: 600;
    text-align: center;
    color: white;
}